/* Intro
 ---------------------------------------------------------------------- */
/* Intro Resize
-------------------------------- */
#intro.intro-resize {
  width: 100%;
  background-size: cover;
  overflow: hidden;
  position: relative; }

#intro .intro-bg {
  /*display: none;*/
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 0;
  opacity: .06;
  filter: alpha(opacity=6);
  -ms-filter: "alpha(opacity=6)";
  background: transparent url(../img/bg_animated.gif) repeat; }

#intro > .container {
  text-align: center;
  width: 100%; }

/* Video header
 -------------------------------- */
#intro.video-header {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  text-align: center; }

/* Intro logo
 -------------------------------- */
#intro-logo {
  margin: 0 auto 40px auto; }

/* Ticker
  --------------------------------*/
#ticker-wrap {
  width: 100%;
  padding: 30px 0 40px;
  /*background: none;
  background: rgba(0,0,0,0.3);*/ }

#ticker {
  height: 110px;
  overflow: hidden;
  text-align: center;
  font-size: 98px;
  font-weight: bold;
  text-transform: uppercase;
  margin-bottom: 0;
  list-style: none; }

#ticker li {
  margin: 0;
  padding: 0 0 20px 0;
  height: 98px;
  color: #fff;
  line-height: normal; }

#ticker li span {
  color: #ff7700; }

/* Header button
 -------------------------------- */
#intro-button {
  display: inline-block;
  margin: 20px auto;
  padding: 14px 30px;
  z-index: 99;
  border: 1px solid #fff;
  font-weight: bold;
  opacity: .7;
  letter-spacing: 4px;
  -webkit-transition: all .2s ease-in-out;
  -moz-transition: all .2s ease-in-out;
  -ms-transition: all .2s ease-in-out;
  -o-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out; }

#intro-button:hover {
  opacity: 1; }

.lt-ie9 #intro-button {
  filter: alpha(opacity=70); }

/* IE8 */
.lt-ie9 #intro-button:hover {
  filter: alpha(opacity=100); }

/* IE8 */
#intro-button span {
  color: #fff;
  font-size: 14px;
  display: inline-block; }

/* Scroll arrow
 -------------------------------- */
a#scroll-arrows {
  width: 39px;
  display: block;
  position: relative;
  top: -14px;
  clear: both;
  margin: 60px auto 0; }
